    position: absolute;
    bottom: -40px;
    padding: 0 60px;
    z-index: 9;
    transform: translateY(-50px);
    display: flex;
    justify-content: space-between;
    width: 100vw;
    box-sizing: border-box;
    font-size: 18px;
    align-items: center;
    color: #fff5ff;
}

@media (max-width: 600px) {
    footer{
        padding: 0 30px;
    }
}

.sns-container{
    display: flex;
    gap: 20px;
    justify-items: center;
}

.sns{
    color: var(--main-color);
    font-size: 20px;
}

footer.dark{
    color: #141414;
}


/* Hub */

.hub-container{
    width: 100%;
    height: 100dvh;
    display: flex;
    gap: 24px;
}

@media (max-width: 600px) {
    .hub-container{
        height: max-content;
        flex-direction: column-reverse;

    }
}

.hub-menu{
    display: flex;
    justify-content: center;
    align-items: center;
    color: #afa5af;
    font-size: 18px;
    font-family: 'Intro', 'Helvetica', sans-serif;
    gap: 20px;
    padding: 0 0 60px 0;
    background-color: #141414;
}

.hub-menu div:hover{
    cursor: pointer;
}

.hub-menu div.active{
    color: #b4d4ee;
    font-weight: bold;
}

.event-info-container{
    height: 100dvh;
    overflow: auto;
}

.event-info-container::-webkit-scrollbar{
    display: none;
}

.event-info{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    margin: 180px 0;
    /* height: 100dvh; */
    overflow-y: auto;
}

@media (max-width: 600px) {
    .event-info-container{
        height: max-content;
    }
    .event-info{
        height: max-content;
        margin: 0;
        gap: 10px;
        margin-bottom: 120px;
    }
}

.slider{
    max-width: 600px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 8px;
    gap: 8px;
    overflow: hidden;
}
.slides{
    width: 100%;
    /* height: 100%; */
    display: flex;
    align-items: center;
    gap: 10px;
    overflow-x: auto;
    scroll-snap-type: x mandatory;
    scroll-behavior: smooth;
    -webkit-overflow-scrolling: touch;
}
.slide{
    width:100%;
    height: 100%;
    max-height: 60dvh;
    display: flex;
    flex-shrink: 0;
    img{
        max-width: 100%;
        max-height: 60dvh;
        width: auto;
        height: auto;
        margin: auto;
        border-radius: 16px;
        scroll-snap-align: center;
    }
}
.slide-controls{
    display: flex;
    gap: 4px;
    a{
        background-color: #505050;
        width: 12px;
        height: 12px;
        border-radius: 50%;
    }
    a.active{
        background-color: #b4d4ee;
    }
}
.slides::-webkit-scrollbar {
    width: 10px;
    height: 10px;
}
.slides::-webkit-scrollbar-thumb {
    background: #505050;
    border-radius: 10px;
}
.slides::-webkit-scrollbar-track {
    background: transparent;
}

.event-name{
    color: #ffa6db;
    font-family: 'Intro', Helvetica, sans-serif;
    text-align: center;
}
.event-details{
    color: #fff5ff;
    font-size: 18px;
}

.ad-headline{
    color: #b4d4ee;
}

@media (min-width: 600px){
    .event-name{
        font-size: 2.5em;
    }
    .ad-headline{
        font-size: 2em;
    }
}

.event-extra-info{
    max-width: 500px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}

.event-extra-info div{
    display: flex;
    gap: 8px;
    color: #fff5ff;
    align-items: center;
}

.hub-separator{
    width: 4px;
    background-color: #b4d4ee;
    margin: 180px 0 80px 0;
}

@media (max-width: 600px){
    .hub-separator{
        height: 2px;
        width: auto;
        margin: 0;
    } 
}

.schedule-wrapper{
    height: 100dvh;
    padding: 180px 0;
    padding-left: 16px;
    box-sizing: border-box;
}

.schedule-container{
    height: 100%;
    max-width: 400px;
    overflow: auto;
    display: flex;
    flex-direction: column;
    gap: 12px;
}

@media (max-width: 600px){
    .schedule-wrapper{
        padding: 0;
        padding-top: 120px;
        height: max-content;
        overflow-x: auto;
    }
    .schedule-container{
        height: 100%;
        max-width: 100%;
        width: 100%;
        display: grid;
        grid-template-columns: repeat(3,100%);
        justify-content: start;
        scroll-snap-type: x mandatory;
        scroll-behavior: smooth;
        -webkit-overflow-scrolling: touch;
    }
    .schedule-item{
        width: 100%;
        flex-grow: 1;
        scroll-snap-align: start;
    }
    .hub-menu{
        padding-bottom: 16px;
    }
}

.schedule-item h2{
    color:#ffa6db;
    font-family: 'Intro', 'Helvetica', sans-serif;
}

.schedule-grid{
    display: grid;
    grid-template-rows: 0fr;
    transition: grid-template-rows 0.3s ease-in, border 0.3s ease-in;
    box-sizing: border-box;
    padding-bottom: 32px;
}
.schedule{
    overflow: auto;
    color:#fff5ff;
    display: flex;
    flex-direction: column;
    gap: 16px;
    padding-left: 16px;
}

.schedule-grid.active{
    grid-template-rows: 1fr;
    border-bottom: solid 1px #ffa6db50;
}

@media (max-width: 600px){
    .schedule-grid{
        padding-bottom: 0;
    }
    .schedule-grid.active{
        border-bottom: none;
    }
    .schedule{
        max-height: 200px;
    }
}

.schedule-container::-webkit-scrollbar{
    display: none;
}

.event-item{
    display: flex;
    align-items: center;
    gap: 8px;
    flex-wrap: wrap;
}

@media (max-width: 600px){
    .event-item{
        flex-wrap: nowrap;
    }
}

.event-item .event-date{
    text-align: center;
    :first-child{
        color: #b4d4ee;
        font-weight: bold;
    }
}

.event-thumb{
    width: 60px;
    height: 60px;
    overflow: hidden;
    display: flex;
    img{
        max-width: 100%;
        max-height: 100%;
        width: auto;
        height: auto;
        margin: auto;
        border-radius: 4px;
    }
    flex-shrink: 0;
}

.event-summary{
    text-align: left;
    margin: 0;
    display: flex;
    flex-direction: column;
    gap: 8px;
    p{
        margin: 0;
        width: 100%;
    }
}

.ad-cta a{
    font-family: 'Intro', 'Helvetica', sans-serif;
    color: #b4d4ee;
}
